<template>
  <div>
    <Sorting @sort="sortData" />
    <hr />

    <table v-if="sortedArray" class="highlight">
      <thead>
        <tr>
          <th>–î–∞—Ç–∞</th>
          <th>–ù–∞–∑–≤–∞–Ω–∏–µ</th>
          <th>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ</th>
          <th>–†–∞—Å—Å—Ç–æ—è–Ω–∏–µ</th>
        </tr>
      </thead>

      <tbody>
        <!-- pageItems - –∏–∑ –º–∏–∫—Å–∏–Ω—ã - –º–∞—Å—Å–∏–≤ –∫–∞—Ä—Ç–æ—á–µ–∫ –Ω–∞ –æ–¥–Ω—É —Å—Ç—Ä–∞–Ω–∏—Ü—É -->
        <tr v-for="item in pageItems" :key="item.code">
          <td>{{ item.date }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.amount }}</td>
          <td>{{ item.distance }}</td>
        </tr>
      </tbody>
    </table>

    <h5 v-else>–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ –ø–æ –∑–∞–¥–∞–Ω–Ω—ã–º –∫—Ä–∏—Ç–µ—Ä–∏—è–º –ø–æ–∏—Å–∫–∞!</h5>

    <Pagination
      :activePage="activePage"
      :allItems="allItems"
      :allPages="allPages"
      :sortedArray="sortedArray"
      @changePage="renderPage"
    />
  </div>
</template>

<script>
import dataArray from '../assets/data'
import Sorting from './Sorting.vue'
import Pagination from './Pagination.vue'
import paginationMixin from '../mixins/pagination.mixin'

export default {
  mixins: [paginationMixin],

  data() {
    return {
      sortedArray: true,
    }
  },

  methods: {
    sortData(sortedArray) {
      sortedArray.length ? (this.sortedArray = true) : (this.sortedArray = false)
      this.setupPagination(sortedArray)
    },
  },

  mounted() {
    this.setupPagination(dataArray)
  },

  components: { Sorting, Pagination },
}
</script>

<style scoped>
th:first-of-type,
th:last-of-type,
th:nth-last-of-type(2) {
  width: 15%;
}
</style>
