<template>
  <h4>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö —Ç–∞–±–ª–∏—Ü—ã</h4>

  <div class="select__container" :class="{ divBottom: !message }">
    <label class="select__label">
      –í—ã–±–µ—Ä–∏—Ç–µ –∫–æ–ª–æ–Ω–∫—É
      <select class="select__list" v-model="selectColumn">
        <option value="" disabled selected>---</option>
        <option value="name">–ù–∞–∑–≤–∞–Ω–∏–µ</option>
        <option value="amount">–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ</option>
        <option value="distance">–†–∞—Å—Å—Ç–æ—è–Ω–∏–µ</option>
      </select>
    </label>

    <label class="select__label">
      –£—Å–ª–æ–≤–∏–µ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
      <select class="select__list" v-model="select–°ondition">
        <option value="" disabled selected>---</option>
        <option value="equals">–†–∞–≤–Ω–æ</option>
        <option value="contains">–°–æ–¥–µ—Ä–∂–∏—Ç</option>
        <option v-if="selectColumn !== 'name'" value="more">–ë–æ–ª—å—à–µ</option>
        <option v-if="selectColumn !== 'name'" value="less">–ú–µ–Ω—å—à–µ</option>
      </select>
    </label>

    <label class="select__label">
      –í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ
      <input
        class="input"
        :class="{ input__disabled: !selectColumn || !select–°ondition }"
        :type="!selectColumn || selectColumn === 'name' ? 'text' : 'number'"
        minlength="1"
        maxlength="25"
        :value="inputValue"
        :disabled="!selectColumn || !select–°ondition"
        v-model="inputValue"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ"
      />
      <span v-if="message">{{ message }}</span>
    </label>
  </div>
</template>

<script>
import dataArray from '../assets/data'

export default {
  // 'sort' - –ø—Ä–∏ –≤—Å–µ—Ö –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö —Å–µ–ª–µ–∫—Ç–∞—Ö –∏ –≤–∞–ª–∏–¥–Ω–æ–º –∏–Ω–ø—É—Ç–µ - —Å—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç –Ω–∞ –∫–∞–∂–¥–æ–µ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è (–±–µ–∑ –∫–Ω–æ–ø–∫–∏)
  emits: ['sort'],

  data() {
    return {
      valid: true,
      inputValue: '',
      selectColumn: '',
      select–°ondition: '',
      initialArray: [],
      sortedArray: [],
      message: '',
    }
  },

  methods: {
    sort(arr) {
      this.selectColumn === 'amount'
        ? arr.sort((a, b) => a.amount - b.amount)
        : arr.sort((a, b) => a.distance - b.distance)
    },

    handleSort(oldValueInput) {
      if (this.selectColumn && this.select–°ondition && !this.message) {
        this.sortedArray = [...this.initialArray]

        switch (this.selectColumn) {
          case 'name':
            if (this.select–°ondition === 'equals') {
              this.sortedArray = this.sortedArray.filter(
                (item) => item.name.toLowerCase() === String(this.inputValue).toLowerCase()
              )
            } else if (this.select–°ondition === 'contains') {
              this.sortedArray = this.sortedArray.filter(
                (item) => item.name.toLowerCase().indexOf(String(this.inputValue).toLowerCase()) > -1
              )
            }
            break

          case 'amount':
            if (this.select–°ondition === 'equals') {
              this.sortedArray = this.sortedArray.filter((item) => +item.amount === +this.inputValue)
            } else if (this.select–°ondition === 'contains') {
              this.sortedArray = this.sortedArray.filter(
                (item) => String(item.amount).indexOf(String(this.inputValue)) > -1
              )
            } else if (this.select–°ondition === 'more') {
              this.sortedArray = this.sortedArray.filter((item) => item.amount > this.inputValue)
              this.sort(this.sortedArray)
            } else if (this.select–°ondition === 'less') {
              this.sortedArray = this.sortedArray.filter((item) => item.amount < this.inputValue)
              this.sort(this.sortedArray)
            }
            break

          case 'distance':
            if (this.select–°ondition === 'equals') {
              this.sortedArray = this.sortedArray.filter((item) => +item.distance === +this.inputValue)
            } else if (this.select–°ondition === 'contains') {
              this.sortedArray = this.sortedArray.filter(
                (item) => String(item.distance).indexOf(String(this.inputValue)) > -1
              )
            } else if (this.select–°ondition === 'more') {
              this.sortedArray = this.sortedArray.filter((item) => item.distance > this.inputValue)
              this.sort(this.sortedArray)
            } else if (this.select–°ondition === 'less') {
              this.sortedArray = this.sortedArray.filter((item) => item.distance < this.inputValue)
              this.sort(this.sortedArray)
            }
            break
        }
      } else if (this.selectColumn && this.select–°ondition && oldValueInput) {
        // –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —Ä–∞–Ω–µ–µ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤ –≤ –∏–Ω–ø—É—Ç (=> oldValueInput) - –æ—Ç—Ä–∏—Å—É–µ—Ç –Ω–∞—á–∞–ª—å–Ω—ã–π –º–∞—Å—Å–∏–≤
        this.sortedArray = [...this.initialArray]
      }
    },
  },

  mounted() {
    this.initialArray = [...dataArray]
  },

  watch: {
    inputValue(value, oldValue) {
      // –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ –∫–æ–ª–∏—á. –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤
      if (this.selectColumn === 'name') {
        if (value.trim().length > 0 && value.trim().length < 3) {
          this.message = '–ú–∏–Ω–∏–º—É–º 3 –∏ –º–∞–∫—Å–∏–º—É–º 25 —Å–∏–º–≤–æ–ª–æ–≤'
        } else {
          this.message = ''
        }
      } else {
        if (String(value.length) > 0 && String(value.length) < 2) {
          this.message = '–ú–∏–Ω–∏–º—É–º 2 –∏ –º–∞–∫—Å–∏–º—É–º 25 —Å–∏–º–≤–æ–ª–æ–≤'
        } else {
          this.message = ''
        }
      }
      this.handleSort(oldValue)
    },

    selectColumn() {
      this.handleSort()
    },

    select–°ondition() {
      this.handleSort()
    },

    sortedArray(value) {
      this.$emit('sort', value)
    },
  },
}
</script>

<style scoped>
.select__container {
  display: flex;
  align-items: flex-start;
  margin: 30px auto 0;
}

.divBottom {
  margin-bottom: 33px;
}

.select__label {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  width: 25%;
  color: black;
  margin-right: 20px;
  font-size: 14px;
  line-height: 1.8;
}

.select__label:last-of-type {
  flex-grow: 2;
}

.select__list {
  display: block;
  height: fit-content;
  border: solid 1px rgb(190, 190, 190);
  border-radius: 3px;
  font-size: 16px;
  outline: none;
  padding: 3px 10px;
}

.input {
  height: 31px !important;
  margin: 0 !important;
  border: 1px solid rgb(190, 190, 190) !important;
  border-radius: 3px !important;
  padding: 3px 10px !important;
  box-sizing: border-box !important;
}

.input__disabled {
  background-color: rgb(223, 223, 223) !important;
}

.input:focus {
  border: 1px solid rgb(0, 153, 133) !important;
}

span {
  color: rgb(185, 0, 0);
}
</style>
